<template>
	<div class="cartItemQty-form">
		<div class="cartItem-mins-btn" @click="minus" :disabled="qty <= 1" > - </div>
		<div class="cartItem-qty-text">{{qty}}</div>
		<div class="cartItem-plus-btn" @click="plus"> + </div>
	</div>
</template>

<script>

export default {
  name: 'CartItemQtyInput',

  props: {
    qty: 1,
  },

  methods: {
    plus () {
      this.qty = this.qty + 1
      this.$dispatch('addQty', this.qty)
    },

    minus () {

      if (this.qty > 1) {
        this.qty = this.qty - 1
        this.$dispatch('minusQty', this.qty)
      }
    }
  }

}
</script>

<style>
	.cartItemQty-form {
		display: flex;
		justify-content: space-between;
		align-items: center;
	}
	.cartItem-plus-btn {
		width: 30px;
		height: 30px;
		border: 0.5px solid #545454;
		border-radius: 5px;
		text-align: center;
		vertical-align: middle;
	}
	
	.cartItem-mins-btn {
		width: 30px;
		height: 30px;
		border: 0.5px solid #545454;
		border-radius: 5px;
		text-align: center;
		vertical-align: middle;
	}
	.cartItem-mins-btn[disabled] {
		border: 1px solid #DDD;
    	color:#ACA899;
	}
</style>


