<template>
  <div id="cart" class="cart-view-container">

    <section class="cart-items">
      <cart-item v-for="cartItem in cartItems" :item="cartItem.item" :qty="cartItem.qty" :color="cartItem.color" :size="cartItem.size">
      </cart-item>
    </section>
    
    <section class="cart-totals">
      <div></div>
      <div class="price">{{ (totalAmount / 100) | currency " " 2  }}</div>
    </section>

  </div>
</template>

<script>
import CartItem from './CartItem.vue'

export default {
  name: 'CartView',

  components: {
    CartItem
  },

  ready() {
    this.getCart()
  },

  data() {
    return {
      cartItems: [],
      totalAmount: 0
    }
  },

  events: {
    plusQty(totalprice) {
      this.totalAmount = this.totalAmount + totalprice
      this.$set("totalAmount", this.totalAmount)
    },

    removeQty(totalprice) {
      this.totalAmount = this.totalAmount - totalprice
      this.$set("totalAmount", this.totalAmount)
    },

    removeItem(item) {
      this.cartItems.pop(item)
      this.updateTotalAmount()
    }
  },

  methods: {
    addQty(cartItem, qty) {
    },

    removeQty(cartItem, qty) {
    }, 

    updateTotalAmount() {
        let totalAmt = 0;
        for(let ci of this.cartItems) {
          var amt = ci.item.price * ci.qty 
          totalAmt = totalAmt + amt
        }
        
        this.$set("totalAmount", totalAmt)
        return totalAmt
      },

    getCart () {
      var vm = this;

      // request callback
      var callback = function(response) {
          console.log("[getCart callback] response: " + JSON.stringify(response.body))
          let cart = response.json()
          let cartItems = cart.cart_items
          let totalAmount = cart.total_amount

          vm.$set('cartItems', cartItems)
          vm.$set('totalAmount', totalAmount)
      }

      var errorCallback = function(response) {
          console.log("[error callback] response: " + response.json());
      }

      // request url 
      var url = "http://lioniq.dev/api/cart";

      // request headers
      var headers = {
          "Content-Type": "application/json",
          "Accept": "application/json",
          "X-APP-KEY": APP_KEY,
          "X-APP-SECRET": APP_SECRET, 
          "X-APP-SHOP-USER": APP_SHOP_USER
      };

      // vue-resource
      this.$http.get(url, {headers: headers}).then(callback, errorCallback);
    }, 
  }
}
</script>

<style>
.cart-view-container {
  position: relative;
}

.cart-totals {
  display: flex;
  justify-content: space-between;
  border-top: 1px solid #ddd;
  background-color: white;
  padding: 20px;
  position: fixed;
  bottom: 0;
  z-index: 100;
}

.cart-item + .cart-item {
  border-top: 1px solid #ddd;
  padding-top: 8px;
  margin-top: 16px;
}
</style>


